<template>
  <AuthContainer
    :steps-count="1"
    :current-step="0"
    class="admin-reset-password"
  >
    <component
      :is="component"
      @submit="onSubmit"
    />
  </AuthContainer>
</template>

<script>
import { useRoute } from '@/shared/plugins/vue-router'
import { defineAsyncComponent, ref } from 'vue'
import AuthContainer from '@/admin/views/Auth/AuthContainer'
import { ROUTES_NAMES } from '@/admin/common/routes'

export default {
  name: 'ResetPassword',
  components: {
    AuthContainer,
    ChangePassword: defineAsyncComponent(() => import('@/admin/views/Auth/ResetPassword/ChangePassword')),
    ResetPasswordForm: defineAsyncComponent(() => import('@/admin/views/Auth/ResetPassword/ResetPasswordForm')),
    ResetPasswordSuccess: defineAsyncComponent(() => import('@/admin/views/Auth/ResetPassword/ResetPasswordSuccess'))
  },
  setup () {
    const route = useRoute()

    const component = ref('ResetPasswordForm')

    if (route.name === ROUTES_NAMES.RESET_PASSWORD_LINK) {
      component.value = 'ChangePassword'
    }

    const onSubmit = () => {
      component.value = 'ResetPasswordSuccess'
    }

    return {
      component,
      onSubmit
    }
  }
}
</script>

